button{
    width: 250px;
    height: 80px;
    font-family: sans-serif;
    font-size: 3em;
    font-weight: bold;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}

button:hover{
    background-color: #00471b;
}

.boton-add{
    background-color: #002ead;
}

.boton-reset{
    background-color: #5a01a7;
}

@media screen and (max-width: 550px) {
    button {
        height: 3.5rem;
        font-size: 2.5em;

      }
}